.Backpack{
    display: flex;
    flex-direction: column;
    justify-self:flex-end;
    min-width: 300px;
    max-width: 500px;
    min-height: 400px;
    background-color: rgb(108, 78, 23);
    border: 9px ridge rgb(82, 58, 15);
    padding: 3px;
}
.Backpack ul{
    list-style: none;
    font-size: 1.2em;
    text-align: start;
    padding-left: 3px;
    max-width: 500px;
}
.Backpack ul li{
    word-wrap: break-word;
    text-align: start;
}
.Backpack ul div{
    width: 100%;
}
.Backpack ul li button{
    margin-left:5px;
}
.Backpack h4{
    text-align: center;
}
.pesoTotal{
    justify-self: flex-end;
}
.BackpackContainer{
    display: flex;
    width:100%;
    flex-direction: column;
    height: 100%;
}
.BackpackContainer p{
    align-self: center;
    justify-self: center;
}
@media screen and (max-width:480px){
    .Backpack{
        display: block;
        width: 90% !important;
        min-width: 250px;
        min-height: 500px;
    }
    .BackpackContainer{
        width: 100% !important;
        font-size: 0.9em;
    }
}