.chest{
    max-width: 150px;
}
.chest img{
    width: 100%;
}
.armorPoints{
    display:flex;
}
.armorBuffs{
    margin-left:10px;
    background-color: rgb(111, 77, 32);
    border:5px groove rgb(111, 77, 32);
    padding:5px;
}
.armorPoints button{
    background-color: #FAA441;
}
.armorPoints button:hover{
    background-color: #f9af64;
}
.armorPoints button:active{
    background-color: #ee9135;
}
.armorBuffs{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin:0;
}
.armorBuffs p{
    margin:0;
    padding:0;
}