
.App > div{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:center;
}
.App > div .backpack{
  align-self: flex-start;
  justify-self: flex-start;
}
.App > div a img{
  max-width: 60px;
}
.App > input{
  width: 0px;
  padding:15px;
  border-color: rgba(255,255,255,0);
  background-image: linear-gradient(to bottom,red,yellow,green,blue,red);
  border-radius: 100%;
}