.CardPlayer{
    max-width: 760px;
    min-height: 450px;
    max-height:500px;
    display: flex;
    box-sizing: border-box;
    border: 3px groove #651f71;
    background-color: #651f71;
}
.CardPlayer input{
    width: 30px;
    height: 15px;
}
.CardPlayer .inputs{
    display: flex;
    flex-direction: column;
    background-color: #506ee5;
}
.CardPlayer p,label{
    font-weight: bolder;
}
.CardPlayer .CardImg{
    width: 300px;
}
.CardPlayer .CardStats{
    min-width: 280px;
    height: 450px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    background-color: #506ee5;
    color:#1d0c20;
}
.CardPlayer .CardStats .Cardponts{
    display:flex;
    flex-direction: column;
}

.CardPlayer .CardStats div{
    width: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    padding-left:5px;
    margin:0;
}
.CardPlayer .CardStats .imageUrl{
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-self: center;
}
.CardPlayer .CardStats .imageUrl label{
    font-size: 0.9em;
}
.image{
    width:350px;
    max-height:450px;
}
.pontos span > button{
    padding:0px;
    width: 10px !important;
    height: 15px;
}
.CardPlayer .image > img{
    width: 100%;
    height:100%;
    max-height:450px;
    object-fit: cover;
}
.CardPlayer .CardStats .imageUrl input{
    width: 90%;
}
.CardPlayer .CardStats div p{
    font-size:0.8rem;
}
.CardPlayer .CardStats input{
    width: 30%;
    height: 73%;
    align-self: center;
    margin-left: 3px;
    background-color: #68b2f8;
}
.CardPlayer .CardStats button{
    align-self: center;
    justify-self: flex-end;
    background-color: #13426d;
    color:white;
    width: 60px;
}
.CardPlayer .CardStats input:focus{
    border: 1px solid #3f88cd;
    outline: 2px solid #13426d;
}
.CardPlayer .CardStats input{
    border: 1px solid #3f88cd;
    color:black;
}

.CardPlayer .CardStats div{
    display: flex;
}

.CardPlayer .CardImg img{
    width: 100%;
    height: 100%;
    object-fit: fill;
}
.testes{
    min-height: 300px;
    max-height: 350px;
    width: 100%;
    text-align: start;
    justify-content: flex-start;
    margin:15px
}
.testes p{
    height: 100%;
}
.Backpack ul button{
    font-size: 0.6rem;
    background-color: rgba(246, 0, 0, 0.5);
}
@media screen and(max-width:480px){
    .CardPlayer{
        display:flex;
        flex-direction: column;
        min-width: 100%;
        max-width: 3000px;
        height:100%;
        width: 100%;
        border: 3px groove #651f71;
    }
    .CardPlayer .CardStats{
        display: inline-block;
        min-width: none;
        max-width: none;
        width: 100%;
        /*height: 296.5px !important;*/
        height: 100%; 
    }
    .CardImg{
        display: block;
        width: 100% !important;
        border-bottom: 3px solid green;
    }
    .CardImg img{
        object-fit: fill !important;
    }
    .CardPlayer .CardStats input{
        width: 35px;
    }
    .CardPlayer .CardStats button{
        margin-top: 5px;
    }
    .CardPlayer .CardStats .testes{
        width: 100%;
    }
    .CardPlayer .CardStats .inputs{
        width: 100%;
        display: inline-flex;
    }
}